                <div class="m-more-action">
                    <div class="u-action" @click="() => (userSelectShow = true)">
                        <svg width="25" height="24" viewBox="0 0 25 24" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M17.5 12a2.25 2.25 0 012.25 2.25V15c0 2.956-2.79 6-7.5 6s-7.5-3.044-7.5-6v-.75A2.25 2.25 0 017 12h10.5zm-5.25-9.75a4.125 4.125 0 110 8.25 4.125 4.125 0 010-8.25z" fill="#FEDAA3"/></svg>
                    </div>
                    <div class="u-action" @click="() => (mapDrawerShow = true)">
                        <svg width="25" height="24" viewBox="0 0 25 24" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" clip-rule="evenodd" d="M10.038 2.611a1.8 1.8 0 00-1.376 0l-4.9 2.028A1.8 1.8 0 002.65 6.3v13.052a1.8 1.8 0 002.489 1.664l4.212-1.743 5.112 2.115a1.8 1.8 0 001.375 0l4.9-2.028a1.8 1.8 0 001.112-1.66V4.646a1.8 1.8 0 00-2.488-1.663L15.15 4.726 10.038 2.61zM9.346 6a.9.9 0 01.9.9v7.8a.9.9 0 11-1.8 0V6.9a.9.9 0 01.9-.9zm6.708 3.3a.9.9 0 10-1.8 0v7.8a.9.9 0 101.8 0V9.3z" fill="#FEDAA3"/></svg>
                    </div>
