                        <svg width="25" height="24" viewBox="0 0 25 24" fill="none" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M16.25 14h-.79l-.28-.27a6.47 6.47 0 001.57-4.23 6.5 6.5 0 10-6.5 6.5c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L21.24 19l-4.99-5zm-6 0c-2.49 0-4.5-2.01-4.5-4.5S7.76 5 10.25 5s4.5 2.01 4.5 4.5-2.01 4.5-4.5 4.5z" fill="#FEDAA3"/></svg>
                    </div>
                </div>
            </template>
            <template #drawerBox2>
                <div
                    class="m-only-no-complete"
                    :class="{ 'is-active': onlyNotCompleted }"
