                >
                    <svg width="40" height="40" viewBox="0 0 40 40" xmlns="http://www.w3.org/2000/svg" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g clip-path="url(#clip0_621_2215)"><path d="M25.62 8.72l-3.54 3.56a8 8 0 00-9.8 9.8l-5.52 5.5c-2.64-2-4.84-4.6-6.36-7.58A22 22 0 0125.6 8.72h.02zm7.6 3.7c2.66 2 4.86 4.6 6.4 7.58a22 22 0 01-25.24 11.28l3.54-3.56a8 8 0 009.8-9.8l5.52-5.5h-.02zm-.5-7.98l2.84 2.84L7.28 35.56l-2.84-2.84L32.72 4.44z" fill="currentColor"/></g><defs><clipPath id="clip0_621_2215"><path fill="currentColor" d="M0 0h40v40H0z"/></clipPath></defs></svg>
